$color: #006d7c;

.detail_btn {
  display: inline-block;
  padding: 6px 58px;
  color: #fff !important;
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  transition: all 0.3s;
  position: relative;
  overflow: hidden;
  z-index: 1;

  &:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $color;
    z-index: -2;
  }

  &:before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0%;
    height: 100%;
    // border: 2px solid #026634;
    background-color: lighten($color, 10%);
    transition: all 0.3s;
    // border-radius   : 10px;
    z-index: -1;
  }

  &:hover {
    color: #fff;

    &:before {
      width: 100%;
    }
  }
}
