.home {
  .home_top {

    .left {
      height: 100%;
    }

    .right {
      border: 1px solid rgb(116, 109, 109);
      box-shadow: 0 0 15px rgb(180, 174, 174);
      padding: 17px;
      .button_area {
        margin-top: 20px;
      }
    }
  }

  .home_bottom {
    margin-top: 50px;
    .flip-box {
      background-color: transparent;
      width: 300px;
      height: 200px;
      border: 1px solid #f1f1f1;
      perspective: 1000px;
    }
    
    .flip-box-inner {
      position: relative;
      width: 100%;
      height: 100%;
      text-align: center;
      transition: transform 0.8s;
      transform-style: preserve-3d;
    }
    
    .flip-box:hover .flip-box-inner {
      transform: rotateY(180deg);
    }
    
    .flip-box-front, .flip-box-back {
      position: absolute;
      width: 100%;
      height: 100%;
      -webkit-backface-visibility: hidden;
      backface-visibility: hidden;
    }
    
    .flip-box-front {
      background-color: #bbb;
      color: black;
    }
    
    .flip-box-back {
      padding: 10px;
      background-color: #006D7C;
      color: white;
      transform: rotateY(180deg);
    }

    a {
      display: block;


      .image_card {
        transition: all 0.3s ease-in-out;
        &:hover{
          transition: all 0.3s ease-in-out;
          box-shadow: 0 0 10px rgb(82, 77, 77);
          transform: scale(1.01);
        }
        img {
          height    : 350px;
          object-fit: fill;
          width     : 100%;
          border: 1px solid black;
        }

      }
    }

    .button_area {
      display        : flex;
      justify-content: center;
      padding        : 60px 0;
    }
  }
}